import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '@/views/HomeView.vue'
import AboutView from '@/views/AboutView.vue'
import AzucenaView from '../views/AzucenaView.vue'
import CaquiView from '../views/CaquiView.vue'
import CosmoView from '../views/CosmoView.vue'
import MaiteView from '../views/MaiteView.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/azucena',
    name: 'azucena',
    component: AzucenaView
  },
  {
    path: '/caqui',
    name: 'caqui',
    component: CaquiView
  },
  {
    path: '/cosmo',
    name: 'cosmo',
    component: CosmoView
  },
  {
    path: '/maite',
    name: 'maite',
    component: MaiteView
  },
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
